/*Основные стили*/

.container {
  max-width: 960px;
  width: 90%;
  margin: 0 auto;
}

.inner-testimonial {
  text-align: center;
  background-color: #fff;
}

.inner-testimonial > img {
  max-width: 128px;
  margin: 0 auto 15px;
  opacity: 0;
  -webkit-transition: all 1.3s linear;
  transition: all 1.3s linear;
}

.owl-item.active .inner-testimonial > img {
  -webkit-transition: all 1.3s linear;
  transition: all 1.3s linear;
}

.owl-item.active .inner-testimonial > img.full-opacity {
  opacity: 1;
  -webkit-transition: all 0.3s linear;
  transition: all 0.3s linear;
}

h3.city-name {
  padding: 10px 0;
  font-size: 20px;
  font-family: "Roboto", sans-serif;
  font-weight: 900;
}

/*p*/

.text-testimonial {
  padding: 10px 0;
  font-size: 20px;
  font-family: "Roboto", sans-serif;
  font-weight: 300;
  line-height: 25px;
}

.block-items {
  cursor: default;
}

/*Изменяем внешний вид элементов управления (точки) */

.owl-theme .owl-dots .owl-dot span {
  width: 15px;
  height: 15px;
  border-radius: 50%;
  border: 1px solid #29323c;
  background: transparent;
}

.owl-theme .owl-dots .owl-dot.active span {
  width: 15px;
  height: 15px;
  background-color: #29323c;
}

/*Стрелки*/

.owl-my-nav {
  position: absolute;
  width: 100%;
  height: 100%;
}

.owl-my-nav .owl-prev,
.owl-my-nav .owl-next {
  position: absolute;
  width: 60px;
  top: 34%;
  font-size: 100px;
  z-index: 20;
  cursor: pointer;
  opacity: 0.5;
  -webkit-transition: opacity .3s ease;
  transition: opacity .3s ease;
}

.owl-my-nav .owl-prev:hover,
.owl-my-nav .owl-next:hover {
  opacity: 0.8;
}

.owl-my-nav .owl-prev {
  left: 50px;
}

.owl-my-nav .owl-next {
  right: 50px;
  text-align: right;
}

* {
  font-family: 'Montserrat', sans-serif;
}

body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

.wrapper {
  max-width: 1170px;
  padding-left: 30px;
  padding-right: 30px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

/* REUSE classes >> */

.btn-radius {
  font-size: 13px;
  color: #e8e8e8;
  text-transform: uppercase;
  font-weight: bold;
  padding: 12px 25px;
  border: 1px solid #f2f2f2;
  border-radius: 24px;
}

.section-heading {
  text-align: center;
  padding-top: 96px;
  margin-bottom: 75px;
  position: relative;
}

.section-heading_white {
  color: #f2f2f2;
}

.section-heading_white::after {
  content: '';
  position: absolute;
  bottom: -15px;
  left: calc(50% - 20px);
  width: 40px;
  height: 2px;
  background-color: #f2f2f2;
}

.section-heading_dark {
  color: #29323c;
}

.section-heading_dark::after {
  content: '';
  position: absolute;
  bottom: -15px;
  left: calc(50% - 20px);
  width: 40px;
  height: 2px;
  background-color: #29323c;
}

.section-heading__title {
  font-size: 26px;
  font-weight: bold;
  text-transform: uppercase;
  padding-bottom: 12px;
}

.section-heading__text {
  font-size: 15px;
  font-weight: 300;
}

/* << REUSE classes */

/* Header >> */

.header {
  background: url("../img/header-bg.jpg") center / cover no-repeat;
}

.header .header-top {
  padding-top: 32px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header .header-top .header-nav__menu {
  font-size: 16px;
  line-height: 36px;
  color: #e8e8e8;
  text-transform: uppercase;
  position: relative;
  padding-right: 30px;
}

.header .header-top .header-nav__menu::after {
  content: '';
  position: absolute;
  background: url("../img/burger.png") center no-repeat;
  width: 21px;
  height: 15px;
  top: 1px;
  right: 0;
}

.header .header-bottom {
  padding-top: 210px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  color: #e8e8e8;
}

.header .header-bottom__title {
  font-size: 36px;
  line-height: 40px;
  font-weight: bold;
  margin-bottom: 28px;
}

.header .header-bottom__text {
  font-size: 17px;
  line-height: 25px;
  font-weight: 300;
  margin-bottom: 30px;
}

.header .header-bottom__btn {
  margin-bottom: 144px;
}

.header .header-bottom .vertical-line {
  width: 50%;
  height: 104px;
  border-right: 1px solid #e8e8e8;
  align-self: flex-start;
  position: relative;
}

.header .header-bottom .vertical-line::after {
  content: '';
  position: absolute;
  top: -50px;
  right: -25px;
  background: url("../img/arrow-down.png") center no-repeat;
  width: 50px;
  height: 50px;
  border: 1px solid #e8e8e8;
  border-radius: 50%;
  box-sizing: border-box;
}

/* << Header */

/* Section services >> */

.services {
  background-color: #f5f5f5;
}

.services .services-list {
  display: flex;
  flex-wrap: wrap;
  margin-left: -7.5px;
  margin-right: -7.5px;
  justify-content: space-between;
  text-align: center;
  color: #29323c;
  padding-bottom: 100px;
}

.services .services-list .services-item {
  width: calc(25% - 15px);
  box-sizing: border-box;
  padding: 0 15px 15px 15px;
}

.services .services-list .services-item__icon {
  margin-bottom: 36px;
}

.services .services-list .services-item__title {
  font-size: 16px;
  font-weight: bold;
  text-transform: uppercase;
  margin-bottom: 20px;
}

.services .services-list .services-item__text {
  font-size: 14px;
}

/* << Section services */

/* Section works >> */

.works .works-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.works .works-list .works-item {
  width: 302px;
  display: block;
  position: relative;
  height: 235px;
}

.works .works-list .works-item__img {
  width: 320px;
  height: 235px;
}

.works .works-list .works-item__text-hover {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  opacity: 0;
  z-index: 99999;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-size: 14px;
  line-height: 20px;
  color: #e8e8e8;
}

.works .works-list .works-item__text-hover strong {
  font-size: 16px;
  font-weight: bold;
  display: block;
}

.works .works-list .works-item__text-hover:hover {
  opacity: 1;
}

/* << Section works */

/* Section reviews >>*/

.reviews .reviews-list {
  padding-bottom: 30px;
}

.reviews .reviews-list .reviews-item {
  text-align: center;
}

.reviews .reviews-list .reviews-item_checked {
  display: block;
}

.reviews .reviews-list .reviews-item__text {
  font-size: 16px;
  line-height: 26px;
  position: relative;
}

.reviews .reviews-list .reviews-item__text:after {
  content: '';
  position: absolute;
  background: url("../img/reviews-ico.png") center no-repeat;
  width: 16px;
  height: 11px;
  bottom: -35px;
  left: calc(50% - 8px);
}

.reviews .reviews-list .reviews-item .reviewer {
  margin-top: 70px;
  align-items: center;
}

.reviews .reviews-list .reviews-item .reviewer__photo {
  display: inline-block;
  border-radius: 50%;
  margin-bottom: 12px;
  width: 64px;
  height: 64px;
}

.reviews .reviews-list .reviews-item .reviewer__name {
  font-size: 15px;
  font-weight: bold;
  color: #29323c;
}

.reviews .reviews-list .reviews-item .reviewer__text {
  font-size: 13px;
  color: #29323c;
}

/* Section people >> */

.people {
  background: url("../img/people-bg.jpg") center / cover no-repeat;
}

.people .people-list {
  display: flex;
  flex-wrap: wrap;
  margin-left: -7.5px;
  margin-right: -7.5px;
  justify-content: space-between;
  padding-bottom: 235px;
}

.people .people-list .people-item {
  background-color: #f2f2f2;
  opacity: .75;
  text-align: center;
  padding-bottom: 25px;
  width: 22.8%;
  margin-bottom: 20px;
}

.people .people-list .people-item .people-photo {
  position: relative;
  height: auto;
  color: #e8e8e8;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 13px;
  display: block;
}

.people .people-list .people-item .people-photo__img {
  width: 270px;
  height: auto;
}

.people .people-list .people-item .people-photo__text-hover {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(41, 50, 61, 0.75);
  opacity: 0;
  z-index: 99999;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-size: 14px;
  line-height: 20px;
  color: #e8e8e8;
}

.people .people-list .people-item .people-photo__text-hover::after {
  content: '';
  position: absolute;
  bottom: 42%;
  left: calc(50% - 20px);
  width: 40px;
  height: 2px;
  background-color: #f2f2f2;
}

.people .people-list .people-item .people-photo__text-hover:hover {
  opacity: 1;
}

.people .people-list .people-item__name {
  font-size: 15px;
  font-weight: bold;
  color: #29323c;
  padding-top: 10px;
}

.people .people-list .people-item__position {
  display: block;
  font-size: 14px;
  color: #29323c;
  padding-top: 5px;
}

.people .people-list .people-item .social-ico {
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 10px;
}

.people .people-list .people-item .social-ico__img {
  margin-right: 5px;
}

.people .people-list .people-item .social-ico__img:last-child {
  margin-right: 0;
}

/* << Section people */

/* Partners >> */

.partners {
  background-color: #f5f5f5;
}

.partners .partners-list {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  padding: 40px 0;
}

.partners .partners-list .partners-item {
  width: 15%;
}

/* << Partners */

/* Contacts >> */

.contacts {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.contacts__wrap {
  width: 50%;
}

.contacts__wrap .map {
  height: 600px;
  width: 100%;
}

.contacts .form-bg {
  background: url("../img/form-bg.jpg") center / cover no-repeat;
}

.contacts .form-bg .form {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  padding: 15% 11% 10% 10%;
  color: #e8e8e8;
  flex-basis: 400px;
}

.contacts .form-bg .form__input {
  min-width: 300px;
  height: 45px;
  border-radius: 2px;
  border: 1px solid #eee;
  background-color: transparent;
  margin-left: 10px;
  margin-bottom: 10px;
  color: #e8e8e8;
  padding: 0 10px;
  box-sizing: border-box;
}

.contacts .form-bg .form input[id='message'] {
  height: 144px;
}

.contacts .form-bg .form__btn {
  border-radius: 2px;
  background-color: transparent;
  border: 1px solid #eee;
  font-size: 13px;
  font-weight: bold;
  color: #e8e8e8;
  padding: 15px 34px;
  cursor: pointer;
}

/* << Contacts */

/* Footer >> */

.footer .wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 30px 5%;
  font-size: 14px;
  color: #29323c;
}

.footer .wrapper .social-ico {
  display: flex;
  align-items: center;
}

.footer .wrapper .social-ico__text {
  margin-right: 20px;
}

.footer .wrapper .social-ico__img {
  margin-right: 5px;
}

.footer .wrapper .social-ico__img:last-child {
  margin-right: 0;
}

/* << Footer */

@media screen and (max-width: 981px) {
  .wrapper {
    padding-left: 20px;
    padding-right: 20px;
  }

  .contacts {
    flex-direction: column;
    align-items: center;
  }

  .contacts__wrap {
    width: calc(100% - 15px);
  }

  .contacts .form-bg .form {
    padding: 20%;
  }

  .contacts .form-bg .form__input {
    margin-left: 0;
    margin-top: 5px;
  }
}

@media only screen and (max-width: 768px) {
  /*p*/

  .text-testimonial {
    font-size: 14px;
  }

  .owl-my-nav .owl-prev,
  .owl-my-nav .owl-next {
    font-size: 50px;
  }

  .owl-my-nav .owl-prev {
    left: 10px;
  }

  .owl-my-nav .owl-next {
    right: 10px;
  }
}

@media screen and (max-width: 748px) {
  .wrapper {
    padding-left: 10px;
    padding-right: 10px;
  }

  .services .services-list .services-item {
    width: calc(50% - 15px);
  }

  .people .people-list {
    justify-content: space-around;
  }

  .people .people-list .people-item {
    width: 270px;
  }

  .contacts .form-bg .form__input {
    display: block;
  }

  .footer .wrapper {
    flex-direction: column;
    align-items: center;
  }

  .footer .wrapper .logo-footer {
    margin-bottom: 15px;
  }

  .footer .wrapper .copyright {
    margin-bottom: 15px;
  }
}

@media screen and (max-width: 576px) {
  .wrapper {
    padding-left: 5px;
    padding-right: 5px;
  }

  .services .services-list .services-item {
    width: calc(100% - 15px);
  }

  .partners .partners-list {
    flex-direction: column;
    align-items: center;
  }

  .partners .partners-list .partners-item {
    width: 28%;
    margin-bottom: 20px;
  }

  .contacts .form-bg .form {
    padding: 3% 10% 3% 3%;
  }
}

@media screen and (max-width: 380px) {
  .wrapper {
    padding-left: 5px;
    padding-right: 5px;
  }

  .contacts .form-bg .form__input {
    min-width: 200px;
  }
}