@import "project.less";
@import "smart-grid.less";
@import "owlstyle.less";

* {
  font-family: 'Montserrat', sans-serif;
}

.reset();

.wrapper {
  .wrapper();
}

a {
  text-decoration: none;
}

/* REUSE classes >> */
.btn-radius {
  font-size: 13px;
  color: @colorLightGrey;
  text-transform: uppercase;
  font-weight: bold;
  padding: 12px 25px;
  border: 1px solid #f2f2f2;
  border-radius: 24px;
}

.section-heading {
  text-align: center;
  padding-top: 96px;
  margin-bottom: 75px;

  position: relative;

  &_white {
    color: @colorWhiteGrey;
    &::after {
      content: '';
      position: absolute;
      bottom: -15px;
      left: ~"calc(50% - 20px)";
      width: 40px;
      height: 2px;
      background-color: @colorWhiteGrey;
    }
  }

  &_dark {
    color: @colorBlackBlue;
    &::after {
      content: '';
      position: absolute;
      bottom: -15px;
      left: ~"calc(50% - 20px)";
      width: 40px;
      height: 2px;
      background-color: @colorBlackBlue;
    }
  }

  &__title {
    font-size: 26px;
    font-weight: bold;
    text-transform: uppercase;
    padding-bottom: 12px;
  }

  &__text {
    font-size: 15px;
    font-weight: 300;

  }
}

/* << REUSE classes */

/* Header >> */
.header {
  background: url("../img/header-bg.jpg") center/cover no-repeat;
  .header-top {
    padding-top: 32px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    .header-nav {

      &__menu {
        font-size: 16px;
        line-height: 36px;
        color: @colorLightGrey;
        text-transform: uppercase;
        position: relative;
        padding-right: 30px;

        &::after {
          content: '';
          position: absolute;
          background: url("../img/burger.png") center no-repeat;
          width: 21px;
          height: 15px;
          top: 1px;
          right: 0;
        }
      }
    }
  }

  .header-bottom {
    padding-top: 210px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    color: @colorLightGrey;

    &__title {
      font-size: 36px;
      line-height: 40px;
      font-weight: bold;
      margin-bottom: 28px;
    }

    &__text {
      font-size: 17px;
      line-height: 25px;
      font-weight: 300;
      margin-bottom: 30px;
    }

    &__btn {
      margin-bottom: 144px;
    }

    .vertical-line {
      width: 50%;
      height: 104px;
      border-right: 1px solid @colorLightGrey;
      align-self: flex-start;

      position: relative;

      &::after {
        content: '';
        position: absolute;
        top: -50px;
        right: -25px;
        background: url("../img/arrow-down.png") center no-repeat;
        width: 50px;
        height: 50px;
        border: 1px solid @colorLightGrey;
        border-radius: 50%;
        box-sizing: border-box;
      }
    }
  }
}
/* << Header */

/* Section services >> */
.services {
  background-color: #f5f5f5;

  .services-list {
    .row-flex();
    justify-content: space-between;
    text-align: center;
    color: @colorBlackBlue;
    padding-bottom: 100px;

    .services-item {
      .size(3);
      .size-sm(6);
      .size-xs(12);
      box-sizing: border-box;
      padding: 0 15px 15px 15px;
      &__icon {
        margin-bottom: 36px;
      }

      &__title {
        font-size: 16px;
        font-weight: bold;
        text-transform: uppercase;
        margin-bottom: 20px;
      }

      &__text {
        font-size: 14px;
      }
    }
  }
}

/* << Section services */

/* Section works >> */
.works {
  .works-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;

    .works-item {
      width: 302px;
      display: block;
      position: relative;
      height: 235px;

      &__img {
        width: 320px;
        height: 235px;
      }

      &__text-hover {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba(0, 0, 0, .5);
        opacity: 0;
        z-index: 99999;
        text-align: center;
        display: flex;
        flex-direction: column;
        justify-content: center;

        font-size: 14px;
        line-height: 20px;
        color: @colorLightGrey;

        strong {
          font-size: 16px;
          font-weight: bold;
          display: block;
        }

        &:hover {
          opacity: 1;
        }

      }
    }
  }
}
/* << Section works */
/* Section reviews >>*/
.reviews {
  //display: flex;
  //flex-direction: column;
  //align-items: center;

  .reviews-list {
    padding-bottom: 30px;
    //position: relative;

    .reviews-item {
      text-align: center;
      //display: none;

      &_checked {
        display: block;
      }

      &__text {
        font-size: 16px;
        line-height: 26px;
        position: relative;

        &:after {
          content: '';
          position: absolute;
          background: url("../img/reviews-ico.png") center no-repeat;
          width: 16px;
          height: 11px;
          bottom: -35px;
          left: ~"calc(50% - 8px)";
        }
      }
      .reviewer {
        margin-top: 70px;
        align-items: center;

        &__photo {
          display: inline-block;
          border-radius: 50%;
          margin-bottom: 12px;
          width: 64px;
          height: 64px;
        }

        &__name {
          font-size: 15px;
          font-weight: bold;
          color: @colorBlackBlue;
        }

        &__text {
          font-size: 13px;
          color: @colorBlackBlue;
        }
      }
    }
  }
}

/* Section people >> */
.people {
  background: url("../img/people-bg.jpg") center/cover no-repeat;
  .people-list {
    .row-flex();
    .sm-block({
      justify-content: space-around;
    });
    justify-content: space-between;
    padding-bottom: 235px;

    .people-item {
      background-color: @colorWhiteGrey;
      opacity: .75;
      text-align: center;
      padding-bottom: 25px;
      width: 22.8%;
      margin-bottom: 20px;

      .sm-block({
        width: 270px;
      });


      .people-photo {
        position: relative;
        height: auto;
        color: @colorLightGrey;
        text-transform: uppercase;
        font-weight: bold;
        font-size: 13px;
        display: block;

        &__img {
          width: 270px;
          height: auto;
        }

        &__text-hover {
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          background: rgba(41,50,61,.75);
          opacity: 0;
          z-index: 99999;
          text-align: center;
          display: flex;
          flex-direction: column;
          justify-content: center;
          font-size: 14px;
          line-height: 20px;
          color: @colorLightGrey;


          &::after {
            content: '';
            position: absolute;
            bottom: 42%;
            left: ~"calc(50% - 20px)";
            width: 40px;
            height: 2px;
            background-color: @colorWhiteGrey;
          }

          &:hover {
            opacity: 1;
          }
        }
      }

      &__name {
        font-size: 15px;
        font-weight: bold;
        color: @colorBlackBlue;
        padding-top: 10px;
      }

      &__position {
        display: block;
        font-size: 14px;
        color: @colorBlackBlue;
        padding-top: 5px;
      }

      .social-ico {
        display: flex;
        align-items: center;
        justify-content: center;
        padding-top: 10px;

        &__img {
          margin-right: 5px;

          &:last-child {
            margin-right: 0;
          }
        }
      }
    }
  }
}
/* << Section people */
/* Partners >> */
.partners {
  background-color: #f5f5f5;

  .partners-list {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    padding: 40px 0;
    .xs-block({
      flex-direction: column;
      align-items: center;
    });
    .partners-item {
      width: 15%;
      .xs-block({
        width: 28%;
        margin-bottom: 20px;
      });
    }

  }
}
/* << Partners */
/* Contacts >> */
.contacts {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  .md-block({
    flex-direction: column;
    align-items: center;
  });

  &__wrap {
    width: 50%;
    .size-md(12);

    .map {
      height: 600px;
      width: 100%;
    }
  }

  .form-bg {
    background: url("../img/form-bg.jpg") center/cover no-repeat;

    .form {
      display: flex;
      flex-direction: column;
      align-items: flex-end;
      padding: 15% 11% 10% 10%;
      color: @colorLightGrey;
      flex-basis: 400px;

      .md-block({
        padding: 20%;
      });
      .xs-block({
        padding: 3% 10% 3% 3%;
      });

      &__input {
        min-width: 300px;
        height: 45px;
        border-radius: 2px;
        border: 1px solid #eee;
        background-color: transparent;
        margin-left: 10px;
        margin-bottom: 10px;
        color: @colorLightGrey;
        padding: 0 10px;
        box-sizing: border-box;

        .md-block({
          margin-left: 0;
          margin-top: 5px;
        });
        .sm-block({
          display: block;
        });
        .xxs-block({
          min-width: 200px;
        });


      }

      input[id='message'] {
        height: 144px;
      }


      &__btn {
        border-radius: 2px;
        background-color: transparent;
        border: 1px solid #eee;
        font-size: 13px;
        font-weight: bold;
        color: @colorLightGrey;
        padding: 15px 34px;
        cursor: pointer;
      }
    }
  }
}
/* << Contacts */
/* Footer >> */
.footer {
  .wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 30px 5%;
    font-size: 14px;
    color: @colorBlackBlue;

    .sm-block({
      flex-direction: column;
      align-items: center;
    });

    .logo-footer {
      .sm-block({
        margin-bottom: 15px;
      });
    }


    .copyright {
      .sm-block({
        margin-bottom: 15px;
      });
    }
    .social-ico {
      display: flex;
      align-items: center;



      &__text {
        margin-right: 20px;
      }

      &__img {
        margin-right: 5px;
        &:last-child {
          margin-right: 0;
        }
      }
    }
  }

}
/* << Footer */
